import React from 'react';
import { Card, CardBody, CardHeader, Col, Row, Button, Table,
    Modal ,ModalHeader, ModalBody, ModalFooter, FormGroup } from 'reactstrap';
import AbstractFormCreator from '../Core/AbstractFormCreator';
import RestProvider from '../Core/RestProvider';


const listStyle = {
    marginTop: '20px'
};

export default class CreateOrder extends AbstractFormCreator {

    constructor(props) {
        super(props);
        this.createElements = this.createElements.bind(this);
        this.createElementsMetadata = this.createElementsMetadata.bind(this);
        this.refineSuggestData = this.refineSuggestData.bind( this );
        this.rest = new RestProvider("createOrder");
        this.confirmDialog = this.confirmDialog.bind( this );
        this.createOrderEntity = this.createOrderEntity.bind( this );
    }

    componentDidMount() {
        let orderId = this.props.match.params.id;
        this.rest.load(orderId)
            .then(resp => {
                let obj = {};
                let options = {};
                resp.data.metadata.forEach(item => {
                    obj["item" + item.id] = "";
                    options["item" + item.id] = item.values;
                });
                obj["metadata"] = resp.data.metadata;
                obj["goodsTitle"] = resp.data.title;
                obj["itemId"] = orderId;
                obj["options"] = options;
                this.setState(obj);
            });
    }

    createSuggest(id, lable) {
        let value = ( this.state.data && this.state.data[id] ? this.state.data[id].id : undefined );
        return { id: id, type: "combo", label: lable, value: value, values: this.state.options ? this.state.options[id] : [],
            convertToVal: it => it.id , convertToStr: it => it.title 
        };
    }

    fetchWidget(suggestType, categoryId, id, value) {
        if (value.reason === 'input-changed') {
            this.rest.getBase(suggestType + "/search", { page: 0, pageSize: 20, 'LIKE:title': value.value, "EQ:category.id": categoryId })
                .then(resp => {
                    let obj = {};
                    obj["suggest" + id] = resp.data.content;
                    this.setState(obj);
                });
        }
    }


    refineSuggestData( id, entity ) {
        let  elId = entity[ id ];
		if( elId ) {
			entity[id] = {
				id: elId
			};
		} else {
            entity[id] = undefined;
        }
    }

    clearWidget() {
        // do nothing
    }

    createElementsMetadata() {
        return this.state.metadata
            .map(element => this.createSuggest("item" + element.id, element.title));
    }

    createElements() {
        if (this.state && this.state.metadata) {
            return this.createElementsMetadata()
                .map(element => this.createComboBox(12, 3, "eidt", element));
        } else {
            return undefined;
        }
    }

    createOrderEntity() {
        let metaData = this.createElementsMetadata();
        let entity = this.refineInternal( metaData );
        metaData.forEach( item => this.refineSuggestData( item.id, entity ) )
        let data = [];
        this.state.metadata.forEach( item => {
            data.push( { metadata: { id: item.id }  , material: { id: entity[ "item" + item.id].id } } );
        });
        return data;
    }

    calculatePrice() {
        this.rest.postCustom( this.state.itemId + "/calculatePrice", this.createOrderEntity() )
            .then( resp => this.setState( { order: resp.data } ) );   
    }


    save() {
        this.rest.postCustom( this.state.itemId + "/submitOrder", this.createOrderEntity() )
            .then( resp => this.props.history.push('/my-orders/') );   
    }

    dismiss() {
        this.setState({ order : undefined })
    }

    confirmDialog() {
        if( this.state && this.state.order ) {
            let tableBody = this.state.order.items.map( item => <tr><td>{item.title}</td><td>{item.price}</td></tr> );
            let price = 0;
            this.state.order.items.forEach( item => price += item.price );
            tableBody.push(  <tr><th> </th><th>{price}</th></tr> );
            return(
                <Modal isOpen={true} size="lg"  >
                    <ModalHeader>  </ModalHeader>
                    <ModalBody>
                        <Table responsive>
                            <thead><tr><th></th><th>()</th></tr></thead>
                            <tbody>{tableBody}</tbody>
                        </Table>
                    </ModalBody>
                    <ModalFooter>
                        <FormGroup>
                            <Button type="submit" size="sm" color="primary" onClick={this.dismiss.bind(this)}></Button>
                            <Button type="submit" size="sm" color="primary" onClick={this.save.bind(this)}> </Button>
                        </FormGroup>
                    </ModalFooter>
                </Modal>);
        } else {
            return undefined;
        }
    }

    render() {
        let elements = this.createElements();
        return (
            <div className="animated fadeIn">
                <Row>
                    {this.confirmDialog()}
                    <Col xs="12">
                        <Card>
                            <CardBody>
                                <Row>   {this.state && this.state.goodsTitle}</Row>
                                <Row style={listStyle}>{elements}</Row>
                                <Row>
                                    <Button type="submit" size="sm" color="primary" onClick={this.calculatePrice.bind(this)}>
                                        <i className="fa fa-plus" aria-hidden="true"></i>  
                                </Button>
                                </Row>
                            </CardBody>
                        </Card>
                    </Col>
                </Row>
            </div>);
    }


}


